import React from 'react'
import CustomModal from '../../atom/modal/index';
import ButtonGreen from 'components/atom/button/ButtonGreen';
import { STATUS_OPTIONS } from 'shared/constants';
import DocumentViewer from 'components/atom/docViewer';

function DocumentViewerModal({ viewDoc, setViewDoc, documentButtonClick, disableKey, setShowReasonModal ,docURL,onLoadError, onLoad}) {
  return (
    <>
      <CustomModal
        //isOpen={viewDoc}
        className="customModal"
        large={true}
        handleToggle={() => setViewDoc((prev) => !prev)}
      >
        <DocumentViewer docURL={docURL} viewDoc={viewDoc} setViewDoc={setViewDoc} onLoadError={onLoadError} onLoad={onLoad}/>

        <div className='d-flex justify-content-center btn_group'>
          <ButtonGreen
            title={STATUS_OPTIONS.VERIFIED.title}
            onClick={() => documentButtonClick(STATUS_OPTIONS.VERIFIED.key)}
            disabled={disableKey === STATUS_OPTIONS.VERIFIED.key}

          />
          <button
            className='btn btn-md btn-danger'
            onClick={() => setShowReasonModal(true)}
            disabled={disableKey === STATUS_OPTIONS.UNVERIFIED.key}
          >
            {STATUS_OPTIONS.UNVERIFIED.title}
          </button>
        </div>
      </CustomModal>
    </>
  )
}

export default DocumentViewerModal
