import React, { useState } from "react";
import { Images } from "shared/images";
import { BUTTONS } from "shared/constants";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { CS_IN_USE_TABLE, PHARMACY_SERVICES_TABLE } from "shared/tableData";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import { splice } from "components/utility";

function ServicesTable({
  data,
  statusChange,
  setmultiplePayload,
  multiplepayload,
  setModalText,
  setShowModal,
  setServiceData,
  deleteService,
}) {
  const [multipleSelect, setMultipleSelect] = useState(false)

  return (
    <>
      <thead>
        <tr key={1000 + 1}>
          <th>
            <input
              type="checkbox"
              checked={multipleSelect}
              onChange={(e) => {
                if (e.target.checked) {
                  setmultiplePayload(data.map((value, index) => value?.id));
                  setMultipleSelect(true)
                } else if (data?.length > multiplepayload.length) {
                  setMultipleSelect(false)
                } else {
                  setmultiplePayload([]);
                  setMultipleSelect(false)
                }
              }}
            />
          </th>
          {Object.values(CS_IN_USE_TABLE).map((i, idx) => (
            <th scope="col" id={idx}>
              <span> {i} </span>
            </th>
          ))}
        </tr>
      </thead>

      {data.length
        ? data.map((value, index) => (
          <tbody key={index}>
            <tr>
              <td>
                <div className="checkbox">
                  <input
                    type="checkbox"
                    onChange={(e) => {
                      if (e.target.checked) {
                        setmultiplePayload((prev) => [...prev, value?.id]);
                      } else {
                        setmultiplePayload(splice(multiplepayload, value?.id));
                        setMultipleSelect(false)
                      }
                    }}
                    checked={multiplepayload?.includes(value.id)}
                  />
                  <label></label>
                </div>
              </td>
              <td>{index + 1}</td>
              <td>{value.cs_name}</td>
              <td>
                <ActivateDeactivate
                  statusChange={statusChange}
                  value={value}
                />
              </td>

              <td>
                <ButtonStatus
                  image={Images.edit}
                  status={"default"}
                  title={"Edit Service"}
                  onClick={() => {
                    setModalText(BUTTONS.EDIT);
                    setServiceData(value);
                    setShowModal(true);
                  }}
                />
                <ButtonStatus
                  image={Images.deleteIcon}
                  status={"Unverified"}
                  title={"Delete Service"}
                  onClick={() => deleteService(value?.id)}
                />
              </td>
            </tr>
          </tbody>
        ))
        : <tbody><tr><td colSpan={6} className="text-center py-5">No Data Availiable</td></tr></tbody>
      }
    </>
  );
}

export default ServicesTable;
