import useNotification from "components/atom/snackbar";
import CustomTable from "components/atom/table";
import DocUnverifyModal from "components/molecules/docUnverifyModal";
import DocumentViewerModal from "components/molecules/documentViewer";
import { filterByDocumentType } from "components/utility";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { changeDocumentStatus } from "redux/actions/auth";
import {
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  STATUS_OPTIONS,
} from "shared/constants";
import { Images } from "shared/images";
import "./styles.scss";
import VerifiedLogo from "components/atom/verifiedIcon";
import { URL_HEAD } from "shared/api";

function BasicPharmacyInfo({ data, reload = () => { } }) {
  const dispatch = useDispatch();
  const [viewDoc, setViewDoc] = useState(false);
  const [showReasonModal, setShowReasonModal] = useState(false);
  const [msg, sendNotification] = useNotification();
  const [docViewerParams, setDocViewerParams] = useState([]);
  const [reasonUnverify, setReasonUnverify] = useState("");

  const documentButtonClick = (isVerified, type) => {
    console.log(type, isVerified, "TYPEE");
    let arr = [
      {
        user_id: data?.id,
        document_type: type ? type : docViewerParams[1],
        ...(isVerified === STATUS_OPTIONS.UNVERIFIED.key && {
          comment: reasonUnverify,
        }),
        verified: isVerified,
      },
    ];

    const payload = {
      doc_status_update: arr,
    };
    documentStatusChange(payload);
  };

  const documentStatusChange = (payload) => {
    dispatch(
      changeDocumentStatus(payload, (res) => {
        setViewDoc(false);
        setShowReasonModal(false);
        reload();
        sendNotification({
          msg: res?.data?.message
            ? res.data.message
            : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setShowReasonModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  return (
    <div className="tab_content">
      <div className="card mb-4">
        <div className="card-body bg-white">
          <div className="row">
            <div className="col-12 col-sm-6 col-md-4 mb-4">
              <div className="user_profile">
                <img
                  src={
                    data?.personal_info?.photo && data?.personal_info?.photo !== "/media/user.png"
                      ? data?.personal_info?.photo + "/" + URL_HEAD
                      : Images.defaultUserPic
                  }
                  alt="profile"
                  width={300}
                  className="w-100"
                />
              </div>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.NAME}
              </label>
              <span className="d-flex">
                {data?.first_name + " " + data?.last_name}
                <VerifiedLogo isVerified={data?.profile_data?.verified} />
              </span>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.EMAIL}
              </label>
              <span className="d-flex">{data?.email}</span>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.ADDRESS}
              </label>
              <span className="d-flex">{data?.address_info}</span>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.MANAGER_NAME}
              </label>
              <span className="d-flex">
                {data?.first_name + " " + data?.last_name}
              </span>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.MANAGER_EMAIL}
              </label>
              <span className="d-flex">{data?.email}</span>
            </div>
            <div className="col-md-4 col-sm-6 mb-3">
              <label className="text-small">
                {PHARMACY_USER_VIEW.BASIC_DETAILS.MANAGER_PHONE}
              </label>
              <span className="d-flex">{data?.phone}</span>
            </div>
          </div>
        </div>
      </div>

      <div className="card mb-4">
        <div className="card-body bg-white">
          <label className="text-small">
            {PHARMACY_USER_VIEW.DESCRIPTIONS}
          </label>
          <p>
            {data?.personal_info?.personal_description
              ? data?.personal_info?.personal_description
              : ""}
          </p>
        </div>
      </div>

      <div className="card mb-4">
        <div className="card-body bg-white">
          <label className="text-small">
            {PHARMACY_USER_VIEW.UPLOADED_DOCS}
          </label>
          <CustomTable>
            <thead>
              <tr key={1000 + 1}>
                {PHARMACY_USER_VIEW.DOCUMENT_TABLE_HEADER.map((i, idx) => (
                  <th scope="col" id={idx}>
                    {i}
                  </th>
                ))}
              </tr>
            </thead>

            {data?.documents?.length ? (
              data?.documents?.map((i, idx) => {
                const documentType = i.document_type;
                const title =
                  PHARMACY_USER_VIEW.DOCUMENT_TYPE_DATA[documentType]?.title;
                return (
                  <tbody>
                    <tr>
                      <td>{idx + 1} </td>
                      <td>{title ? title : ""} </td>
                      <td
                        onClick={() => {
                          setViewDoc(true);
                          setDocViewerParams([
                            i?.id,
                            documentType,
                            filterByDocumentType(
                              data?.documents,
                              documentType
                            ),
                          ]);
                        }}
                      >
                        {i?.document ? i?.name : "Not Uploaded"}
                      </td>
                      <td>
                        <div className="d-flex">
                          <button
                            className="btn btn-sm btn-secondary m-1"
                            onClick={() =>
                              documentButtonClick(
                                STATUS_OPTIONS.VERIFIED.key,
                                documentType
                              )
                            }
                            disabled={
                              i?.verified === STATUS_OPTIONS.VERIFIED.key
                            }
                          >
                            {STATUS_OPTIONS.VERIFIED.title}
                          </button>
                          <button
                            className="btn btn-sm btn-primary m-1"
                            onClick={() => {
                              setShowReasonModal(true);
                              setDocViewerParams([
                                i?.id,
                                documentType,
                                filterByDocumentType(
                                  data?.documents,
                                  documentType
                                ),
                              ]);
                            }}
                            disabled={
                              i?.verified === STATUS_OPTIONS.UNVERIFIED.key
                            }
                          >
                            {STATUS_OPTIONS.UNVERIFIED.title}
                          </button>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                );
              })
            ) : (
              <tbody>
                <tr>
                  <td colSpan={5} className="text-center py-5">
                    No Documents Uploaded
                  </td>
                </tr>
              </tbody>
            )}
          </CustomTable>
        </div>
      </div>

      <DocumentViewerModal
        viewDoc={viewDoc}
        setViewDoc={setViewDoc}
        documentButtonClick={documentButtonClick}
        //disableKey={docViewerParams[2]}
        setShowReasonModal={setShowReasonModal}
      />

      <DocUnverifyModal
        show={showReasonModal}
        setShow={setShowReasonModal}
        onClick={() => documentButtonClick(STATUS_OPTIONS.UNVERIFIED.key)}
        reason={reasonUnverify}
        setReason={setReasonUnverify}
      />
    </div>
  );
}

export default BasicPharmacyInfo;
