import ButtonGreen from "components/atom/button/ButtonGreen";
import CustomModal from "components/atom/modal";
import React from "react";
import { STATUS_OPTIONS } from "shared/constants";
import { Images } from "shared/images";
import './style.scss';

function StatusChangeModal({ show, setShow, documentButtonClick, docType }) {
  return (
    <>
      <CustomModal
        isOpen={show}
        avarge={true}
        handleToggle={() => setShow((prev) => !prev)}
      >
        <div className="confirmation_modal text-center">
          <em className="mb-3 modal_icon success_icon">
            <img src={Images.verifyUnverifyIcon} alt="" width="30" />
          </em>

          <h2 className="h5 mb-2">{docType.title}</h2>
          <p className="mb-4">Are you sure want to verify/unverified all document?</p>

          <div className="d-flex justify-content-center text-center flex-wrap btn_group">
            <ButtonGreen
              title={STATUS_OPTIONS.VERIFIED.title}
              onClick={() => { documentButtonClick(STATUS_OPTIONS.VERIFIED.key) }}
            />
            <button className="btn btn-md btn-danger"
              onClick={() => documentButtonClick(STATUS_OPTIONS.UNVERIFIED.key)}
            >
              {STATUS_OPTIONS.UNVERIFIED.title}
            </button>
          </div>
        </div>
      </CustomModal>
    </>
  );
}

export default StatusChangeModal;
